<template>
<div class="content">
    <div class="content__side-bar">
        <sideBar></sideBar>
    </div>
    <div class="content__main">
        <div class="content__main--title">
            <div class="header__title">
                <div class="meets__title">
                    <button class="meets__title--filter">
                        <img class="coverImg" src="../assets/icon/filter.png" alt="">
                    </button>
                    <h2 class="meets__title--item">Meet</h2>
                    <h2 class="meets__title--item active">Likes</h2>
                </div>
            </div>
        </div>
        <div class="content__main--content">
           <card v-if="showNormal" :tstatus="hideTitle"></card>
           <mask-card v-if="showMask"></mask-card>
           <mk-card v-if="showMask"></mk-card>
        </div>
    </div>
    <div class="overlay"></div>
</div>
</template>

<script>
import SideBar from '../components/sidebar.vue';
import Card from '../components/card/card.vue';
import MaskCard from '../components/card/maskCard.vue';
import MkCard from '../components/card/maskCard-mobile.vue';
export default {
    name: 'Meets',
    components: {
        SideBar,
        Card,
        MaskCard,
        MkCard
    },
    data() {
        return {
            hideTitle: true,
            type: 'mask'
        }
    },
    computed: {
        showNormal() {
           return this.type === 'normal' || this.type == '';
        },
        showMask() {
            return this.type === 'mask';
        }
    }
}
</script>

<style lang="scss">
.meets__title {
    display: flex;
    flex-direction: row;
    justify-content: center;
    
    &--item {
        font-size: 40px;
        color: #272938;
        line-height: 97px;
        padding: 0 40px;
        box-sizing: border-box;
        cursor: pointer;
        border-bottom: 2px solid transparent; 
        &.active {
            color: #FF00FF;
            border-bottom: 2px solid #FF00FF;
        }
    }
    &--filter {
        width: 40px;
        background: none;
        border: none;
        display: none;
        @media screen and (max-width: 768px) {
            display: inline-flex;
        }
    }
    @media screen and (max-width: 768px) {
        &--item {
            line-height: 47px;
            font-size: 20px;
            img {
                display: block;
                width: 100%;
                height: auto;
                object-fit: cover;
            }
        }
    }
}
</style>